<template>
  <div class="publish-container">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item to="/"></el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.query.id ? '' : '' }}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-form
        ref="publish-form"
        :rules="formRules"
        :model="article"
        label-width="80px"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="article.title"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <el-input type="textarea" v-model="article.content"></el-input> -->
          <el-tiptap
            v-model="article.content"
            :extensions="extensions"
            height="350"
            placeholder=""
          ></el-tiptap>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="article.cover.type">
            <el-radio :label="1"></el-radio>
            <el-radio :label="3"></el-radio>
            <el-radio :label="0"></el-radio>
            <el-radio :label="-1"></el-radio>
          </el-radio-group>
          <!--
             article.cover.images 
            
             $event
           -->
          <template v-if="article.cover.type > 0">
            <upload-cover
              :key="cover"
              v-for="(cover, index) in article.cover.type"
              v-model="article.cover.images[index]"
            />
            <!-- <upload-cover
              :key="cover"
              v-for="(cover, index) in article.cover.type"
              :cover-image = "article.cover.images[index]"
              @update-cover = "onUpdateCover(index, $event)"
            /> -->
          </template>
        </el-form-item>
        <el-form-item label="" prop="channel_id">
          <el-select v-model="article.channel_id" placeholder="">
            <el-option
              :label="channel.name"
              :value="channel.id"
              v-for="(channel, index) in channels"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onPublish(false)"></el-button>
          <el-button @click="onPublish(true)"></el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import UploadCover from './components/upload-cover'
import {
  getArticlesChannels,
  addArticle,
  getArticle,
  updateArticle
} from '@/api/article'
import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Image,
  Strike,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  HorizontalRule,
  Fullscreen,
  Preview,
  CodeBlock,
  TextColor
} from 'element-tiptap'
import 'element-tiptap/lib/index.css'
import { uploadImage } from '@/api/image'

export default {
  name: 'PublishIndex',
  props: {},
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  data () {
    return {
      channels: [], // 
      article: {
        title: '', // 
        content: '', // 
        cover: { // 
          type: 1, // 
          images: [] // 
        },
        channel_id: null
      },
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 6 }), // 
        new Bold({ bubble: true }), // 
        new Image({
          //  base64 
          uploadRequest (file) {
            //  Content-Type  multipart/form-data FormData
            const fd = new FormData()
            fd.append('image', file)
            // 1 return  Promise 
            // axios  Promise 
            return uploadImage(fd).then(res => {
              //  return 
              return res.data.data.url
            })
          } //  Promise<url>
        }),
        new Underline(), // 
        new Italic(), // 
        new Strike(), // 
        new HorizontalRule(), // 
        new ListItem(),
        new BulletList(), // 
        new OrderedList(), // 
        new TodoItem(),
        new TodoList(),
        new Fullscreen(),
        new Preview(),
        new CodeBlock(),
        new TextColor()
      ],
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 30, message: ' 5  30 ', trigger: 'blur' }
        ],
        content: [
          {
            validator (rule, value, callback) {
              if (value === '<p></p>') {
                // 
                callback(new Error(''))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '' }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created () {
    this.loadChannels()
    // 
    if (this.$route.query.id) {
      this.loadArticle()
    }
  },
  mounted () {},
  methods: {
    loadChannels () {
      getArticlesChannels().then((res) => {
        this.channels = res.data.data.channels
      })
    },
    onPublish (draft = false) {
      this.$refs['publish-form'].validate(valid => {
        if (!valid) {
          return
        }
        const articleId = this.$route.query.id
        if (articleId) {
          updateArticle(articleId, this.article, draft).then(res => {
            // console.log(res)
            this.$message({
              message: `${draft ? '' : ''}`,
              type: 'success'
            })
            this.$router.push('/article')
          })
        } else {
          addArticle(this.article, draft).then(res => {
            this.$message({
              message: '',
              type: 'success'
            })
          })
        }
      })
    },
    loadArticle () {
      // console.log('loadArticle')
      // 
      getArticle(this.$route.query.id).then(res => {
        // 
        this.article = res.data.data
      })
    }
    // onUpdateCover (index, url) {
    //   this.article.cover.images[index] = url
    // }
  }
}
</script>

<style lang='less' scoped></style>
